* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

input:focus,
input:active,
button:focus,
button:active {
  outline: none;
}
input::placeholder {
  color: rgba(255, 255, 255, 0.42);
}

.App {
  background-image: url("./assets/tmp-background.png");
  background-size: cover;
  background-repeat: no-repeat;

  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.App:before,
.App:after {
  content: '';
  display: block;
  width: 100%;

  position: fixed;
  bottom: 0;
}
.App:before {
  background-color: rgba(61, 61, 61, 0.7);

  top: 0;
  z-index: 0;
}
.App:after {
  width: 1124px;
  height: 925px;

  background-image: url("./assets/pattern.png");

  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
}

.window {
  display: flex;
  width: 300px;

  flex-direction: column;
  align-items: center;

  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
}

.window > .logotype {
  display: block;
  width: 196px;
  height: 130px;

  background-image: url("./assets/logotype.png");
}

.window > .title {
  display: flex;
  width: 100%;

  justify-content: center;

  color: #fff;

  font-family: "Proxima Nova", sans-serif;
  font-size: 24px;
}

.window > .controls {
  display: flex;
  width: 100%;

  flex-direction: column;

  margin-top: 51px;
}
.window > .controls > input {
  width: 300px;
  height: 49px;

  background: none;
  color: #fff;

  font-family: "Proxima Nova", sans-serif;
  font-size: 14px;

  text-align: center;

  border: 1px solid #EDA10E;
  border-radius: 5px;
}
.window > .controls > input:not(:first-child) {
  margin-top: 19px;
}
.window > .controls > button {
  width: 100%;
  height: 49px;

  background: linear-gradient(103.31deg, rgba(255, 255, 255, 0.42) -20.79%, rgba(255, 255, 255, 0) 56.47%), #EDA10E;
  color: #fff;

  font-family: "Proxima Nova", sans-serif;
  font-size: 18px;

  border: unset;
  border-radius: 5px;

  margin-top: 23px;
}
.window > .controls > button:hover {
  cursor: pointer;
  filter: brightness(1.1);
}

.window > .footer {
  display: flex;
  width: 100%;

  justify-content: center;

  margin-top: 19px;
}
.window > .footer > span {
  color: #A7A3A3;

  font-family: "Proxima Nova", sans-serif;
  font-size: 18px;
}
.window > .footer > span:last-child {
  color: #fff;

  margin-left: 5px;
}
.window > .footer > span:last-child:hover {
  cursor: pointer;

  text-decoration: underline;
}
.window.Register > .footer > span {
  text-decoration: underline;
}